import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Input,
  Tooltip,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col,
  Divider,
  // message,
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/purchaseReturnReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectPT = "/dashboard/purchases/purchase-returns";
const redirectPR = "/dashboard/purchases/purchase-receipts";
const redirectPI = "/dashboard/purchases/purchase-invoices";

class PurchaseReturnReport extends Component {
  state = {
    filter: {
      id_supplier: null,
      id_location: null,
      status: null,
      start_date: moment().startOf('month').format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      sort_by: "return_date",
      sort_type: "asc",
      // pagination: null
    },
    search: null,
  };

  // Get All data diawal
  componentDidMount() {
    const { getPurchaseReturnReports } = this.props;
    getPurchaseReturnReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.purchaseReturnReport.error) {
      notification("error", this.props.purchaseReturnReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.purchaseReturnReport.success) {
      notification("success", this.props.purchaseReturnReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getPurchaseReturnReports } = this.props;
    let filter = this.filterValue();

    getPurchaseReturnReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getPurchaseReturnReports } = this.props;
    this.setState({
      filter: {
        id_supplier: null,
        id_location: null,
        status: null,
        start_date: moment().startOf('month').format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "return_date",
        sort_type: "asc",
        // pagination: null
      }
    });
    this.setState({ search: null });
    getPurchaseReturnReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    }
    else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    }
    else if (name === "id_location") {
      this.setState({
        filter: {
          ...this.state.filter,
          id_location: value,
          id_supplier: null
        }
      });
    }
    else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
          "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
          "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_supplier) {
      string
        ? (string += "&id_supplier=" + filter.id_supplier)
        : (string += "id_supplier=" + filter.id_supplier);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    string
      ? (string +=
        "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
        "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string
  }

  // Handle filter
  handleFilter = () => {
    const { getPurchaseReturnReports } = this.props;
    //const { current_page } = this.props.purchaseReturnReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getPurchaseReturnReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getPurchaseReturnReports } = this.props;
    this.setState({
      filter: {
        id_supplier: null,
        id_location: null,
        status: null,
        start_date: moment().startOf('month').format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "return_date",
        sort_type: "asc",
        // pagination: null
      }
    });
    this.setState({ search: null });
    getPurchaseReturnReports();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activatePurchaseReturnReport(id);
    } else {
      this.props.deactivatePurchaseReturnReport(id);
    }
  };

  // // Handle pagination
  // handlePageChange = page => {
  //   const { getPurchaseReturnReports } = this.props;
  //   const { search, filter } = this.props.purchaseReturnReport;
  //   getPurchaseReturnReports(page, search, filter);
  // };

  render() {
    // console.log(this.props);

    // Data setup
    const { purchaseReturnReport } = this.props;
    const { purchase_return_reports,
      locations,
      suppliers,
      filter,
      filter_select,
      loading,
      loadingForeign,
    } = this.props.purchaseReturnReport;
    //console.log(this.props.purchaseReturnReport.base_currency); //console.log(this.state);

    // Definisi kolom2 serta field2 API yang dipakai tabel 
    const summariesColumns = [
      {
        title: getLanguage({ id: "totalTransaction" }),
        dataIndex: "total_data",
        rowKey: "total_data",
        //width: "10%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "totalItem" }),
        dataIndex: "total_item",
        rowKey: "total_item",
        //width: "10%",
        align: "right",
        render: (text) => {
  
          const obj = {
            children: text,
            props: {},
          };
          if (!isNaN(text)) {
            obj.props.rowSpan = 2;
          }
          // These two are merged into above cell
          else {
            obj.props.rowSpan = 0;
          }
          return obj;
  
        }
        // parseFloat(text)
        //   .toFixed(0)
        //   .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
      },
      {
        title: getLanguage({ id: "totalQuantity" }),
        dataIndex: "total_qty",
        rowKey: "total_qty",
        //width: "10%",
        align: "right",
        render: text =>
          <NumberFormat value={parseFloat(text)} />
      },
      {
        title: `${getLanguage({ id: "totalDiscount" })} (IDR)`,
        dataIndex: "total_discount",
        rowKey: "total_discount",
        //width: "20%",
        align: "right",
        render: text =>
          <NumberFormat value={parseFloat(text)} />
      },
      {
        title: `${getLanguage({ id: "totalTax" })} (IDR)`,
        dataIndex: "total_tax",
        rowKey: "total_tax",
        //width: "20%",
        align: "right",
        render: text =>
          <NumberFormat value={parseFloat(text)} />
      },
      {
        title: `${getLanguage({ id: "total" })} (IDR)`,
        dataIndex: "total_amount",
        rowKey: "total_amount",
        //width: "20%",
        align: "right",
        render: text =>
          <NumberFormat value={parseFloat(text)} />
      },
    ]
  
    const columns = [
      {
        title: getLanguage({ id: "purchaseReturns.purchaseReturnNo" }),
        dataIndex: "purchase_return_no",
        rowKey: "purchase_return_no",
        //width: "15%",
        fixed: "left",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "pending";
              break;
            case 2:
              className = "processed";
              break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "pending";
              break;
          }
  
          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <TransNo>
                <Link to={`${redirectPT}/update/${row.id_purchase_return}`}>
                  {text}
                </Link>
              </TransNo>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "purchaseReturnDate" }),
        dataIndex: "return_date",
        rowKey: "return_date",
        //width: "10%",
        date: true,
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "transaction_no",
        rowKey: "transaction_no",
        //width: "15%",
        render: (text, row) => {
          let redirect;
  
          if (row.transaction_type === 1) {
            redirect = redirectPR;
          }
          else if (row.transaction_type === 2) {
            redirect = redirectPI;
          }
  
          return (
            <div style={{ display: "flex", height: "20px" }}>
              <TransNo>
                <Link to={`${redirect}/update/${row.id_transaction}`}>
                  {row.transaction_no}
                </Link>
              </TransNo>
            </div>
          );
  
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        //width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.supplier" }),
        dataIndex: "supplier_name",
        rowKey: "supplier_name",
        //width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "totalQuantity" }),
        dataIndex: "total_qty",
        rowKey: "total_qty",
        //width: "15%",
        align: "right",
        parse: true,
        render: text =>
          <NumberFormat value={parseFloat(text)} />
      },
      {
        title: `${getLanguage({ id: "totalDiscount" })} (IDR)`,
        dataIndex: "total_discount",
        rowKey: "total_discount",
        //width: "15%",
        align: "right",
        parse: true,
        render: text =>
          <NumberFormat value={parseFloat(text)} />
      },
      {
        title: `${getLanguage({ id: "totalTax" })} (IDR)`,
        dataIndex: "total_tax",
        rowKey: "total_tax",
        //width: "15%",
        align: "right",
        parse: true,
        render: text =>
          <NumberFormat value={parseFloat(text)} />
      },
      {
        title: `${getLanguage({ id: "total" })} (IDR)`,
        dataIndex: "total_amount",
        rowKey: "total_amount",
        //width: "15%",
        align: "right",
        parse: true,
        render: text =>
          <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "6%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "processedBy" }),
        dataIndex: "processed_by",
        rowKey: "processed_by",
        width: "6%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status_string",
        rowKey: "status_string",
        width: "6%",
        align: "center",
        render: (text, row) => {
          let className;
          let display;
          switch (row.status) {
            case 1:
              className = "pending";
              display = getLanguage({ id: "pending" });
              break;
            case 2:
              className = "processed";
              display = getLanguage({ id: "processed" });
              break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "pending";
              display = getLanguage({ id: "pending" });
              break;
          }
  
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    const dataSource = [];
    const summariesDataSource = []

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    purchase_return_reports.map((data) => {
      let status_string;

      if (data.status === 2) {
        status_string = getLanguage({ id: "processed" })
      }
      else if (data.status === 3) {
        status_string = getLanguage({ id: "cancelled" })
      }
      else {
        status_string = getLanguage({ id: "pending" })
      }

      dataSource.push({
        ...data,
        status_string
      })
    })

    summariesDataSource.push({
      // name: messages.all_page ? messages.all_page : "All Page",
      total_amount: purchaseReturnReport.total_amount ? purchaseReturnReport.total_amount : 0,
      total_discount: purchaseReturnReport.total_discount ? purchaseReturnReport.total_discount : 0,
      total_qty: purchaseReturnReport.total_qty ? purchaseReturnReport.total_qty : 0,
      total_tax: purchaseReturnReport.total_tax ? purchaseReturnReport.total_tax : 0,
      total_item: purchaseReturnReport.total_item ? purchaseReturnReport.total_item : 0,
      total_data: purchaseReturnReport.total ? purchaseReturnReport.total : 0,
    })

    // definisi kolom untuk pdf export (dengan mengubah semua properti fixed menjadi false)
    var pdfColumns = columns.map(data => {
      data.fixed = false
      return data
    })
    
    const fileName = getLanguage({ id: "purchaseReturnReports.pageHeader" })

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="purchaseReturnReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="purchaseReturnReports.pageHeader" />}
              summariesColumns={this.summariesColumns}
              summariesData={summariesDataSource}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => document.title = siteConfig.documentTitle}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                  "zoom: 0.8;" +
                  "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={pdfColumns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              margin=".7cm"
              scale={0.35}
              summariesColumns={this.summariesColumns}
              summariesData={summariesDataSource}
              title="purchaseReturnReports.pageHeader"
            />

            <ExcelWrapper
              title="purchaseReturnReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button style={{ marginLeft: "5px" }} onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={`${getLanguage({ id: "purchaseReturns.purchaseReturnNo" })}, ${getLanguage({ id: "createdBy" })}, ${getLanguage({ id: "processedBy" })}`}
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={['1']}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="suppliers.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_supplier"
                      )}
                      value={
                        this.state.filter.id_supplier
                          ? this.state.filter.id_supplier
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        this.state.filter.id_location ? (
                          <IntlMessages id="select.supplier" />
                        ) : (
                            <IntlMessages id="noLocation" />
                          )
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      notFoundContent={
                        this.state.filter.id_location
                          ?
                          (
                            <Empty description={<IntlMessages id="noData" />} />
                          )
                          : (
                            <Empty
                              description={
                                <IntlMessages id="noLocation" />
                              }
                            />
                          )
                      }
                    >
                      {
                        this.state.filter.id_location
                          ?
                          suppliers.map((data, key) => (
                            data.id_location === this.state.filter.id_location
                              ?
                              (
                                <Option key={key} value={data.id_supplier}>
                                  {`${data.supplier_code} (${data.supplier_name})`}
                                </Option>
                              )
                              : null
                          ))
                          : null
                      }
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="purchaseInvoices.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                          this.state.filter.end_date
                          ? [
                            moment(this.state.filter.start_date),
                            moment(this.state.filter.end_date)
                          ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>

                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="stockValuationReports.sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "return_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                        //allowClear={true}
                        //showArrow={false}
                        >
                          <Option key={1} value="return_date">
                            <IntlMessages id="purchaseReturnReports.filter.date" />
                          </Option>
                          <Option key={2} value="purchase_return_no">
                            <IntlMessages id="purchaseReturns.purchaseReturnNo" />
                          </Option>
                          <Option key={3} value="location_name">
                            <IntlMessages id="sidebar.location" />
                          </Option>
                          <Option key={4} value="supplier_name">
                            <IntlMessages id="sidebar.supplier" />
                          </Option>
                          <Option key={5} value="total_amount">
                            <IntlMessages id="total" />
                          </Option>
                          <Option key={6} value="total_qty">
                            <IntlMessages id="totalQuantity" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                        //allowClear={true}
                        //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Row gutter={1} type="flex">
                      <Col span={12}>
                        <label>Status</label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "status"
                          )}
                          value={
                            this.state.filter.status
                              ? this.state.filter.status
                              : []
                          }
                          style={{ width: "95%" }}
                          placeholder="Status"
                          optionFilterProp="children"
                          filterOption={true}
                          // loading={this.props.location.loadingFilterSites}
                          allowClear={true}
                        //showArrow={false}
                        >
                          <Option value={1}>{getLanguage({ id: "pending" })}</Option>
                          <Option value={2}>{getLanguage({ id: "processed" })}</Option>
                          <Option value={3}>{getLanguage({ id: "cancelled" })}</Option>
                        </Select>
                      </Col>
                      <Col
                        span={8}
                        style={{
                          marginTop: "24px"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clicksearchFilter" />} />
            ) :
              purchase_return_reports.length === 0 &&
                !this.props.purchaseReturnReport.loading ? (
                  <Empty
                    description={<IntlMessages id="purchaseReturnReports.table.noData" />}
                  />
                ) : (
                  <div className="isoInvoiceTable">
                    <Divider orientation="left" style={{ fontSize: 14 }}>
                      <IntlMessages id="summaries" />
                    </Divider>
                    <Scrollbars style={{ width: "100%" }}>
                      <TableWrapper
                        dataSource={summariesDataSource}
                        columns={summariesColumns}
                        loading={loading}
                        className="invoiceListTable"
                        bordered={true}
                        pagination={false}
                      />
                    </Scrollbars>
                    <Divider orientation="left" style={{ fontSize: 14 }}>
                      <IntlMessages id="details" />
                    </Divider>
                    <Scrollbars style={{ width: "100%" }}>
                      <TableWrapper
                        dataSource={dataSource}
                        columns={columns}
                        loading={loading}
                        className="invoiceListTable"
                        bordered={true}
                        pagination={false}
                      />
                    </Scrollbars>
                  </div>
                )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    purchaseReturnReport: state.purchaseReturnReport,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(PurchaseReturnReport);
